

.message{
  display: flex;
  margin: 40px 0;
  color: #fff;
  background-color: rgb(25, 27,30);
}
.message.sent{
  flex-direction: row-reverse;
}

.message img{
  border-radius: 50%;
  margin: 0 20px
}
.message-container form{
  position: fixed;
  bottom: 0;
  width: 70%;
  right: 0%;
}

.name{
  text-transform: capitalize;
  text-align: end;
  color: #eea324;
}
.content{
  text-align: left;
}
.message-container{
  width: 70%;
  margin-left: auto;
  background: rgb(25, 27,30);
  height: calc(100vh - 100px);
  overflow-y: scroll;
}
.sidebar{
  position: fixed;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  width: 30%;
  top: 0;
  margin-right: auto;
  background: rgb(29, 33, 39);
  height: 100vh;
}
.sidebar h1{
  color: #fff;
}
form{
  position: fixed;
  bottom: 0;
  width: 70%;
  right: 0;
  height: 100px;
  background-color:rgb(31, 33, 37)
}
.form__field{
  width: 90%;
}
.form__button{
  width: 10%;
}
.sing-in_container{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.signin_btn{
  cursor: pointer;
}





@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
