@import url(https://fonts.googleapis.com/css?family=Lato:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.message{
  display: flex;
  margin: 40px 0;
  color: #fff;
  background-color: rgb(25, 27,30);
}
.message.sent{
  flex-direction: row-reverse;
}

.message img{
  border-radius: 50%;
  margin: 0 20px
}
.message-container form{
  position: fixed;
  bottom: 0;
  width: 70%;
  right: 0%;
}

.name{
  text-transform: capitalize;
  text-align: end;
  color: #eea324;
}
.content{
  text-align: left;
}
.message-container{
  width: 70%;
  margin-left: auto;
  background: rgb(25, 27,30);
  height: calc(100vh - 100px);
  overflow-y: scroll;
}
.sidebar{
  position: fixed;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  width: 30%;
  top: 0;
  margin-right: auto;
  background: rgb(29, 33, 39);
  height: 100vh;
}
.sidebar h1{
  color: #fff;
}
form{
  position: fixed;
  bottom: 0;
  width: 70%;
  right: 0;
  height: 100px;
  background-color:rgb(31, 33, 37)
}
.form__field{
  width: 90%;
}
.form__button{
  width: 10%;
}
.sing-in_container{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.signin_btn{
  cursor: pointer;
}





@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.form__group{position:relative;padding:15px 0 0;margin-top:10px;width:50%}.form__field{font-family:inherit;width:90%;border:0;border-bottom:2px solid #9b9b9b;outline:0;font-size:1.3rem;color:#fff;padding:7px 0;background:transparent;transition:border-color 0.2s}.form__field::-webkit-input-placeholder{color:transparent}.form__field:-ms-input-placeholder{color:transparent}.form__field::placeholder{color:transparent}.form__field:-ms-input-placeholder ~ .form__label{font-size:1.3rem;cursor:text;top:20px}.form__field:placeholder-shown ~ .form__label{font-size:1.3rem;cursor:text;top:20px}.form__label{position:absolute;top:0;display:block;transition:0.2s;font-size:1rem;color:#9b9b9b}.form__field:focus{padding-bottom:6px;font-weight:700;border-width:3px;border-image:linear-gradient(to right, #11998e, #38ef7d);border-image-slice:1}.form__field:focus ~ .form__label{position:absolute;top:0;display:block;transition:0.2s;font-size:1rem;color:#11998e;font-weight:700}.form__field:required,.form__field:invalid{box-shadow:none}@keyframes topAnimation{from{transform:translate(0rem, 0)}to{transform:translate(0rem, 3.5rem)}}@keyframes bottomAnimation{from{transform:translate(-11.5rem, 0)}to{transform:translate(0rem, 0)}}button.btn{text-transform:uppercase;text-decoration:none;cursor:pointer;font-weight:700;border:0;position:relative;letter-spacing:0.15em;margin:0 auto;padding:1rem 2.5rem;background:transparent;outline:none;font-size:28px;color:#ececec;transition:all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55) 0.15s}button.btn::after,button.btn::before{border:0;content:"";position:absolute;height:40%;width:10%;transition:all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);z-index:-2;border-radius:50%}button.btn::before{border:0;background-color:#c92918;top:-0.75rem;left:0.5rem;animation:topAnimation 2s cubic-bezier(0.68, -0.55, 0.265, 1.55) 0.25s infinite alternate}button.btn::after{background-color:#e74c3c;top:3rem;left:13rem;animation:bottomAnimation 2s cubic-bezier(0.68, -0.55, 0.265, 1.55) 0.5s infinite alternate}button.btn:hover{color:white}button.btn:hover::before,button.btn:hover::after{top:0;height:100%;width:100%;border-radius:0;animation:none}button.btn:hover::after{left:0rem}button.btn:hover::before{top:0.5rem;left:0.35rem}

